<template>
  <div class="panel panel-info">
    <div class="panel-body">
      <div v-show="page.datas!=[]" style="display:none">
        <table class="table table-bordered table-striped table-hover">
          <thead>
            <tr>
              <th><input type="checkbox" name="" id=""></th>
              <th v-for=" (title,index) in page.titleData" :key="index" v-text="title"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(datas,index) in page.datas" :key="index">
              <td><input type="checkbox" name="" id="" @change="updateCheckData(datas,$event)" v-model="check[check+index]"></td>
              <td v-for="(data,index) in datas" :key="index" v-html="data"></td>
            </tr>
          </tbody>
        </table>
        <!--  -->
        <ul class="pagination col-sm-12" >
          <div class="col-sm-3">
            <a class="btn disabled" v-html="''+page.active+''"></a>
            <a class="btn disabled" v-html="''+page.pageTotal+''"></a>
          </div>
          <li @click="pageClick(pages)" v-for="pages in page.pages" :key="pages" :class="{'active':page.active==pages}">
            <!--class="{{addClass(pages)}} :class="{true:'active'}[page.active==pages]""-->
            <a href="javascript:void(0);" v-text="pages"></a>
          </li>
          <div class="col-sm-4">
            <div class="col-sm-4">
              <select class="form-control" v-model="page.pagelist" @change="countclick">
                <option v-for="(item,index) in page.pageselect" :key="index" :value="item" v-text="item" :selected="item==page.count"></option>
              </select>
            </div>
            <div class="col-sm-3 loginpage">
              <input type="text" class="form-control" v-model="loginpage" placeholder="" style="font-size:6px">
            </div>
            <div class="col-sm-2">
              <button class="btn btn-info" @click="pageClick(loginpage)"></button>
            </div>
          </div>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data: function() {
    return {
      check: {},
      loginpage: "",
      inputinfor: false
    };
  },
  props: ["pageloginData"],
  mounted: function() {
    var vm = this;
    vm. loginclick();
  },
  computed:{
    page(){
      return this.$store.getters.GETUSERTABLEPAGE;
    }
  },
  methods: {
    //
    countclick() {
      this.allcheck = false;
      vm.$store.commit("UPDATEPAGEACTIVE",1);
      this.loginclick();
    },
    //
    loginclick() {
      var vm = this;
      vm.$api.http.loginTableData(vm);
    },
    //
    pageClick(page) {
      var Active = page; //
      var active = this.page.active; //
      var total = this.page.pageTotal; //
      if (Active == active) {
        this.loginpage = "";
        return;
      } else if (Active > total) {
        alert("" + Active + "");
        this.loginpage = "";
        return;
      } else if (Active == "") {
        Active = 1;
      } else if (Active == "") {
        Active = total;
      } else if (Active == "") {
        if (active == 1) {
          return;
        }
        Active = active - 1;
      } else if (Active == "") {
        if (active == total) {
          return;
        }
        Active = active + 1;
      } else if (Active < 1) {
        Active = 1;
      }
      this.loginpage = "";
      if (this.loginclick && typeof this.loginclick === "function") {
        this.$store.commit("UPDATEPAGEACTIVE", Active);
        this.loginclick();
      }
    },
    //title
    judgeTitle(data) {
      var datas = data;
      if (datas.length) {
        for (var i = 0; i < datas.length; i++) {
          if (datas[i] == "") {
            datas = datas.splice(i, 1);
          }
        }
        return datas;
      } else {
        return [];
      }
    },
    datafilter(value, type) {
      //100
      function addZero(val) {
        if (val < 10) {
          return "0" + val;
        } else {
          return val;
        }
      }
      var date = new Date(Number(value));
      var year = addZero(date.getFullYear());
      var month = addZero(date.getMonth() + 1);
      var day = addZero(date.getDate());
      if (type == "yyyyMMdd") {
        return year + "" + month + "" + day + "";
      }
    },
    //
    judgeData(data) {
      var vm = this;
      var datas = data;
      if (datas.length > 0) {
        datas.forEach(function(Data) {
          for (var key in Data) {
            switch (key) {
              case "password":
                delete Data[key];
                break;
              case "regtime":
                Data[key] = vm.datafilter(Data[key], "yyyyMMdd");
                break;
              case "state":
                console.log(Data[key]);
                if (Number(Data[key]) == 0) {
                  Data[key] = "<div style='background:#ccc'></div>";
                } else if (Number(Data[key]) == 1) {
                  Data[key] = "<div style='background:#00ff00'></div>";
                } else if (Number(Data[key]) == 2) {
                  Data[key] = "<div style='background:#ff000f'></div>";
                }
                break;
              case "role_state":
                if (Number(Data[key]) == 0) {
                  Data[key] = "<div style='background:#ccc'></div>";
                } else if (Number(Data[key]) == 1) {
                  Data[key] = "<div style='background:#00ff00'></div>";
                } else if (Number(Data[key]) == 2) {
                  Data[key] = "<div style='background:#ff000f'></div>";
                }
                break;
                case "type":
                if (Number(Data[key]) == 0) {
                  Data[key] = "<div style='background:#ccc'></div>";
                } else if (Number(Data[key]) == 1) {
                  Data[key] = "<div style='background:#00ff00'></div>";
                } else if (Number(Data[key]) == 2) {
                  Data[key] = "<div style='background:#ff000f'></div>";
                }
                break;
            }
          }
        });
      } else {
        datas = "";
      }
      return datas;
    },
    //
    pagelogin(total, active, count) {
      var from = active - Math.floor(count / 2); //
      var to = active + Math.floor(count / 2); //
      if (from <= 0) {
        from = 1;
        to = from + count - 1;
      }
      if (to > total) {
        to = total;
        from = to - count + 1;
      }
      var range = [];
      for (var i = from; i <= to; i++) {
        if (i > 0) {
          range.push(i);
        }
      }
      if (range.length > 0) {
        range.push("");
        range.unshift("");
        range.push("");
        range.unshift("");
      }
      var pages = range;
      return pages;
    },
    //
    updateCheckData(data, e) {
      var vm = this;
      var check = e.target.checked;
      var checkData = vm.page.checkData;
      var index = checkData.indexOf(data);
      if (check) {
        checkData.push(data);
      } else {
        checkData.splice(index, 1);
      }
      vm.$store.commit("UPDATECHECKDATA",checkData);
      vm.$api.opreate.emitCall("tableCheck", vm.page.checkData, vm);
    },
    //
    checkEmpty() {
      this.check = {};
      this.$store.commit("UPDATECHECKDATA",[]);
      this.$api.opreate.emitCall("tableCheck", this.page.checkData, this);
    }
  },
  watch: {
    pageloginData: function(news, olds) {
      console.log(news);
      this.loginclick();
      this.inputinfor = false;
      this.page.checkData = [];
      this.checkEmpty();
    }
  }
};
</script>

<style scoped>
</style>



