<template>
    <div>
         <div class="col-sm-12">
            <div class="col-sm-2">
                <h5 ><span v-text="statetitle"></span>{{opreatetitle}}</h5>
            </div>
            <div class="col-sm-12">
                <form class="form form-horizontal col-sm-8" name="myform">
                  <!--   -->
                   <div v-if="stateId=='gongnengguanli'"  class="form-group">
                      <!--  -->
                      <label for="" class="col-sm-2 control-label"></label>
                        <div>
                          <div class="col-sm-1">
                            <input type="radio" name="type" id="type1" value="1" v-model="addData.type"><label for="type1" class="control-label"></label> 
                          </div>
                          <div class="col-sm-1">
                            <input type="radio" name="type" id="type2" value="2" :checked="true" v-model="addData.type"><label for="type2"  class="control-label"></label> 
                          </div>
                          <div class="col-sm-1">
                            <input type="radio" name="type" id="type3" value="3" v-model="addData.type"><label for="type3"  class="control-label"></label> 
                          </div>
                        </div>
                   </div>
                   <!--  -->
                    <div class="form-group" >
                        <label class="control-label col-sm-2"><span v-text="statetitle"></span></label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('name')&&addData.name!='','has-error':errors.has('name')}">
                            <input type="text" class="form-control" name="name" :placeholder="''+statetitle+',20'" v-model="addData.name" :data-vv-as="statetitle+''"
                             v-validate="validateAdd('name')">
                            </div>
                             <div class="text-danger">
                                 <span v-show="errors.has('name')">{{errors.first("name")}}</span>
                        </div>
                    </div>
                    <!--  -->
                    <div v-if="stateId=='gongnengguanli'">
                      <!--url-->
                      <div class="form-group" v-if="addData.type==2">
                        <label class="control-label col-sm-2">url</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('url')&&addData.url!='','has-error':errors.has('url')}">
                            <input type="text" name="url" class="form-control" placeholder="url," v-model="addData.url" 
                              data-vv-as="url"   v-validate="validateAdd('url')"></div>
                             <div  class="text-danger">
                            <span v-show="errors.has('url')">{{errors.first("url")}}</span>
                        </div>
                      </div>
                      <!--  -->
                      <div class="form-group">
                         <label class="control-label col-sm-2"></label>
                        <div class="col-sm-6" >
                            <select type="text" name="url" class="form-control"  v-model="addData.application"><option :value="item.application_system_id" v-for="(item,index) of applications" :key="index" v-text="item.system_name"></option> </select></div>
                      </div>
                      <!--  -->
                      <div class="form-group">
                          <label for="" class="col-sm-2 control-label"></label>
                          <div class="col-sm-6">
                            <input type="text" class="form-control"  v-model="addData.parentName" @click="loginmenuData" readonly="readonly">
                          </div>
                      </div>
                        <!--  -->
                      <div class="form-group" v-if="addData.type!=1">
                          <label for="" class="col-sm-2 control-label"></label>
                          <div class="col-sm-6" :class="{'has-success':!errors.has('perms')&&addData.perms!='','has-error':errors.has('perms')}">
                            <input type="text" class="form-control" placeholder="user:list,user:delete" v-model="addData.perms" name="perms" data-vv-as="" v-validate="'required'">
                          </div>
                          <div class="text-danger">
                            <span v-show="errors.has('perms')">{{errors.first("perms")}}</span>
                          </div>
                      </div>
                        <!--  -->
                      <div v-if="addData.type!=3">
                          <div class="form-group">
                          <label for="" class="col-sm-2 control-label"></label>
                          <div class="col-sm-6" :class="{'has-success':!errors.has('order_num')&&addData.order_num!='','has-error':errors.has('order_num')}">
                            <input type="number" class="form-control" v-model="addData.order_num" data-vv-as="" v-validate="'required'" name="order_num">
                          </div>
                          <div class="text-danger">
                            <span v-show="errors.has('order_num')">{{errors.first("order_num")}}</span>
                          </div>
                      </div>
                        <!--  -->
                      <div class="form-group">
                          <label for="" class="col-sm-2 control-label"></label>
                          <div class="col-sm-6">
                            <input type="text" class="form-control" placeholder="" name="icon" v-model="addData.icon">
                          </div>
                      </div>
                        </div>
                    </div>
                     <!--   -->
                    <div class="form-group">
                        <label class="control-label col-sm-2"></label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('mydetail')&&addData.detail!='','has-error':errors.has('mydetail')}">
                            <textarea rows="6" class="form-control" name="mydetail" placeholder="50" v-model="addData.detail" data-vv-as=""
                            v-validate="'required|cnlength:50'"></textarea>
                            </div>
                         <div class="text-danger">
                          <span v-show="errors.has('mydetail')">{{errors.first("mydetail")}}</span>
                        </div>
                    </div>
                    <!--  -->
                    <div class="form-group col-sm-12">
                        <button type="button" class=" col-sm-2 col-sm-offset-2 btn btn-info" @click="addDatas"></button> 
                        <button type="button" class=" col-sm-2 col-sm-offset-2  btn btn-warning" @click="inforAddclick"></button> 
                    </div>
                </form>
            </div>
            <div id="menuLayer" style="display: none;padding:10px;">
                <ul id="menuTree" class="ztree"></ul>
            </div>
        </div>
    </div>
</template>
<script>
import ztree from "ztree";
import "ztree/css/metroStyle/metroStyle.css";
import "ztree/js/jquery.ztree.all.min.js";
export default {
  data: function() {
    return {
      initData: {
        id: "",
        name: "",
        url: "",
        detail: "",
        type: "2",
        order_num: 0,
        perms: "",
        parentName: "",
        parentId: 0,
        application:""
      },
      addData: {},
      nameVer: "",
      urlVer: "",
      ztree: {},
      opreatetitle: "",
      applications: [],
      menuData: []
    };
  },

  props: {
    stateId: String,
    statetitle: String,
    opreate: String
  },
  mounted: function() {
    this.logincheckData();
    const validate = (value, args) =>
      new Promise(resolve => {
        if (value) {
          var data = {
            data: {
              data: value,
              name: args[0]
            },
            states: {
              id: this.stateId
            }
          };
          return resolve(
            this.$axios
              .post(this.HOST + "inspectData.action", data)
              .then(function(success) {
                console.log(success.data);
                if (success.data.code == "0") {
                  return {
                    valid: true
                  };
                } else if ((success.data.code = "500")) {
                  return {
                    valid: false,
                    data: {
                      message: success.data.msg
                    }
                  };
                }
              })
              .catch(function(error) {
                console.log(error);
                console.log("");
                return {
                  valid: false,
                  data: {
                    message: ""
                  }
                };
              })
          );
        } else {
          return false;
        }
      });
    const val = {
      getMessage: function(name) {
        return "";
      },
      validate: validate
    };
    this.$validator.extend("val", val);
    this.getMenu();
  },
  filters: {},
  watch: {
    checkData(newvalue) {
      this.logincheckData();
      if (this.stateId == "gongnengguanli") {
        this.loginApplicationData();
      }
    },
    opreate(news) {
      this.logincheckData();
    }
  },
  computed: {
    checkData() {
      return this.$store.getters.GETUSERTABLEPAGECHECKDATA;
    }
  },
  methods: {
    //
    loginmenuData() {
      var vm = this;
      if(vm.addData.application!=""){
        vm.$axios
        .post("selectMenu.action")
        .then(success => {
          if (success.data.code == "0") {
            vm.menuData = success.data.data;
            vm.menuTree();
          } else {
          }
        })
        .catch(error => {
          console.log(error);
        });
      }else{
        vm.$layer.alert("",{icon:0,time:1000});
      }
    },
    //
    loginApplicationData() {
      var vm = this;
      vm.$axios
        .post("selectApplicationId.action")
        .then(success => {
          if (success.data.code == "0") {
            vm.applications = success.data.data;
          } else {
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    //
    logincheckData() {
      var data;
      if (this.opreate == "update") {
        if (this.checkData.length > 0) {
          data = this.$api.opreate.ObjectCopy(this.checkData);
          this.addData = this.cutStr("detail", "name", data[0]);
          this.opreatetitle = "";
        }
      } else if (this.opreate == "add") {
        this.addData = this.$api.opreate.ObjectCopy(this.initData);
        this.opreatetitle = "";
      }
    },
    //
    cutStr(name1, name2, Obj) {
      var obj = Obj;
      for (var key in obj) {
        if (key.indexOf(name1) > -1 && key != name1) {
          obj[name1] = obj[key];
          delete obj[key];
        }
        if (key.indexOf(name2) > -1 && key != name2) {
          obj[name2] = obj[key];
          delete obj[key];
        }
      }
      return obj;
    },
    //
    validateAdd(name) {
      var Str = "required|cnlength:20";
      var Addstr = Str + "|val:" + name;
      if (this.opreatetitle == "") {
        return Addstr;
      } else {
        return Str + "";
      }
    },
    //
    inforAddclick() {
      this.addData = this.$api.opreate.ObjectCopy(this.initData);
      this.$validator.errors.clear();
      this.$api.opreate.emitCall("inforCall", false, this);
    },
    //
    addDatas() {
      var vm = this;
      var stateId = vm.stateId;
      var data = vm.addData;
      var datas = {
        data: data,
        states: {
          id: stateId
        }
      };
      // 
      if (vm.opreatetitle == "") {
        if (vm.stateId == "gongnengguanli") {
          vm.$api.http.opreateHttp(
            vm,
            "updatePageInf.action",
            datas,
            "",
            vm.tableData
          );
        } else if (vm.stateId == "jueseguanli") {
          vm.$api.http.opreateHttp(
            vm,
            "updatRole.action",
            datas,
            "",
            vm.tableData
          );
        }
      } else if (vm.opreatetitle == "") {
        //
        vm.$api.http.opreateHttp(
          vm,
          "distributor.action",
          datas,
          "",
          vm.tableData
        );
      }
    },
    //
    menuTree() {
      var vm = this;
      layer.open({
        type: 1,
        offset: "50px",
        skin: "layui-layer-molv",
        title: "",
        area: ["300px", "450px"],
        shade: 0,
        shadeClose: false,
        content: jQuery("#menuLayer"),
        btn: ["", ""],
        btn1: function(index) {
          var node = vm.ztree.getSelectedNodes();
          //
          vm.addData.parentName = node[0].name;
          vm.addData.parent_id = node[0].menuId;
          layer.close(index);
          console.log(vm.addData);
        }
      });
    },
    //
    getMenu(menuId) {
      var setting = {
        data: {
          simpleData: {
            enable: true,
            idKey: "menuId",
            pIdKey: "parentId",
            rootPId: -1
          },
          key: {
            url: "nourl"
          }
        }
      };
      //
      //ztree
      var vm = this;
      vm.ztree = $.fn.zTree.init($("#menuTree"), setting, vm.menuData);
      //updatetree
      var node = vm.ztree.getNodeByParam("menuId", vm.addData.parentId);
      //tree
      vm.ztree.selectNode(node);
      //update
      vm.addData.parentName = node.name;
    }
  }
};
</script>






